.sidebar {

}

.sidebar.open {
    position: absolute;
    width: 317px;
    height: 720px;
    left: 0px;
    top: 0px;

    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
}

.background {
    position: absolute;
    width: 100%;
    height: 720px;
    left: 0px;
    top: 0px;

    background: #000000;
    opacity: 0.3;
}

.nav {
    position: absolute;
    width: 72px;
    height: 27px;
    left: 70px;
    top: 135px;

    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    /* identical to box height */


    color: #000000;
}

.nav a {
}

.nav a:hover {
}

.nav a.active {
    position: absolute;
    width: 114px;
    height: 27px;
    left: 70px;
    top: 63px;

    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    /* identical to box height */

    text-decoration-line: underline;

    color: #0059B2;
}
