.burgerMenuIcon {
    width: 24px;
    height: 24px;

    margin: auto 0px auto 70px;
}

.header {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13), 0px 0px 2px rgba(29, 33, 38, 0.1);
}

.header h1 {
    margin: auto 70px auto 0px;
    width: 124px;
    height: 29px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;

    color: #000000;
}